.wrapper {
    // width: 100%;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 10px;
}

.logoBlock {
    &__logo {
        display: flex;
        gap: 5px;
        align-items: center;
    }

    & p {
        color: #241400;
        line-height: 100%;
    }

    & img {
        width: 42px;
        height: 32.79px;
    }
}

.logoText {

    &__secondText {
        font-style: normal;
        font-weight: 400;
        font-size: 13px;
        display: flex;
        align-items: center;
        color: #241400;
    }

    &__bible {
        border-bottom: 2px solid gray;
        font-style: normal;
        font-weight: 900;
        font-size: 14px;
        line-height: 17px;
        color: #4D2900;
    }
}

.burgerMenu {
    // display: none;

    &__main {
        display: none;
    }

    &__userInterfaceBlock {
        display: flex;
        align-items: center;
        gap: 30px;
    }


    @media screen and (max-width:950px) {
        display: flex;
        align-items: center;
        gap: 15px;

        &__main {
            display: flex;

        }
    }
}



.phoneNumber {
    display: none;
}

