@media screen and (max-width:420px) {

    .wrapper {
        padding: 7px;
    }

    .logoBlock {
        // border: 1px solid red;   
        align-self: start;
    }

    .burgerMenu {
        display: flex;
        flex-direction: column-reverse;


        &__main {

            align-self: end;
        }

        &__userInterfaceBlock {}
    }
}